




























import { AppService } from "@/service/app.service";
import { CafeSettings } from "@/x-vue/interfaces/interfaces";
import Vue from "vue";
import Component from "vue-class-component";

@Component({})
export default class CafeCreate extends Vue {
  app = AppService.instance;
  countries = {};
  cafeSettings: CafeSettings = {} as CafeSettings;

  form = {
    countryCode: "",
    domain: "",
    rootDomain: "",
  };

  get isCountryDomain(): boolean {
    return !!this.cafeSettings.countryDomains?.includes(this.app.rootDomain);
  }

  async mounted(): Promise<void> {
    try {
      this.cafeSettings = Object.assign({}, await this.app.api.loadCafeSettings());
      this.countries = Object.assign({}, await this.app.api.countryAll());
    } catch (e) {
      this.app.error(e);
    }
  }

  async onSubmit(): Promise<void> {
    if (this.isCountryDomain) {
      this.form.countryCode = this.cafeSettings.mainCafeSettings[this.app.rootDomain].countryCode;
    }
    this.form.rootDomain = this.app.rootDomain;
    try {
      await this.app.createCafe(this.form);
    } catch (e) {
      this.app.error(e);
    }
  }
}
